        handleReachBottom () {
          return new Promise(resolve => {
            setTimeout(() => {
              const last = this.list1[this.list1.length - 1];
              for (let i = 1; i < 11; i++) {
                this.list1.push(last + i);
              }
              resolve();
            }, 1000);
          });
        },
        tiao (id){
          this.$router.push({name:'detail',params:{id:id}})
        }
      }
    }
</script>

